import Vue from 'vue'
import Router from 'vue-router'
import Layout from '../layout'

Vue.use(Router)

/**
 * hidden: true                   true( false)
 * meta : {
    title: 'title'               
    icon: 'icon-name'            
    fixed: true                   true tag  tag ( false)
  }
 * */

export default new Router({
  routes: [
    {
      path: '/login',
      component: () => import('../views/Login'),
      hidden: true
    },
    {
      path: '/404',
      component: () => import('../views/404'),
      hidden: true
    },
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      redirect: '/home',
      children: [{
        path: 'home',
        name: 'Home',
        component: () => import('../views/Home'),
        meta: {
          title: '',
          icon: 'vue-sys-icon-index',
          fixed: true
        }
      }]
    },
    {
      path: 'https://github.com/LoveHins/vue-admin-system',
      name: 'doc',
      component: Layout,
      meta: {
        title: '',
        icon: 'vue-sys-icon-wendang'
      }
    },
    {
      path: '/form-table',
      name: 'FormTable',
      component: Layout,
      redirect: '/form-table/table-classic',
      meta: {
        title: '&',
        icon: 'vue-sys-icon-biaoge'
      },
      children: [
        {
          path: 'table-classic',
          name: 'TableClassic',
          component: () => import('../views/form-table/TableClassic'),
          meta: {
            title: ''
          }
        },
        {
          path: 'form-list',
          name: 'FormList',
          component: () => import('../views/form-table/FormList'),
          meta: {
            title: ''
          }
        },
        {
          path: 'table-inline-edit',
          name: 'TableInlineEdit',
          component: () => import('../views/form-table/TableInlineEdit'),
          meta: {
            title: ''
          }
        }
      ]
    },
    {
      path: '/editors',
      name: 'Editors',
      component: Layout,
      redirect: '/editors/markdown',
      meta: {
        title: '',
        icon: 'vue-sys-icon-bianjiqi'
      },
      children: [
        {
          path: 'markdown',
          name: 'Markdown',
          component: () => import('../views/editors/MarkdownEditor'),
          meta: {
            title: 'Markdown'
          }
        },
        {
          path: 'rich-text',
          name: 'RichText',
          component: () => import('../views/editors/RichTextEditor'),
          meta: {
            title: ''
          }
        }
      ]
    },
    {
      path: '/tools',
      name: 'Tools',
      component: Layout,
      redirect: '/tools/image-upload',
      meta: {
        title: '',
        icon: 'vue-sys-icon-zujian'
      },
      children: [
        {
          path: 'image-upload',
          name: 'ImageUpload',
          component: () => import('../views/tools/ImageUpload'),
          meta: {
            title: ''
          }
        },
        {
          path: 'drag',
          name: 'Drag',
          component: () => import('../views/tools/Drag'),
          redirect: '/tools/drag/drag-list',
          meta: {
            title: ''
          },
          children: [
            {
              path: 'drag-list',
              name: 'DragList',
              component: () => import('../views/tools/Drag/DragList'),
              meta: {
                title: ''
              }
            }
            // {
            //   path: 'vue-drr',
            //   name: 'VueDrr',
            //   component: () => import('../views/tools/Drag/VueDrr'),
            //   meta: {
            //     title: ''
            //   }
            // }
          ]
        },
        {
          path: 'transfer',
          name: 'Transfer',
          component: () => import('../views/tools/TransferPage'),
          meta: {
            title: ''
          }
        },
        {
          path: 'count-to',
          name: 'CountTo',
          component: () => import('../views/tools/CountToPage'),
          meta: {
            title: ''
          }
        }
      ]
    },
    // {
    //   path: '/image',
    //   name: 'Image',
    //   component: Layout,
    //   redirect: '',
    //   meta: {
    //     title: '',
    //     icon: 'vue-sys-icon-picture'
    //   },
    //   children: [
    //     {
    //       path: 'image-cropper',
    //       name: 'ImageCropper',
    //       component: () => import('../views/image/ImageCropper'),
    //       meta: {
    //         title: ''
    //       }
    //     },
    //     {
    //       path: 'image-synthesizer',
    //       name: 'ImageSynthesizer',
    //       component: () => import('../views/image/ImageSynthesizer'),
    //       meta: {
    //         title: ''
    //       }
    //     }
    //   ]
    // },
    {
      path: '/tree',
      name: 'Tree',
      component: Layout,
      redirect: '/tree/org-tree',
      meta: {
        title: '',
        icon: 'vue-sys-icon-shuxing'
      },
      children: [
        {
          path: 'org-tree',
          name: 'OrgTree',
          component: () => import('../views/tree/OrgTree'),
          meta: {
            title: ''
          }
        },
        {
          path: 'ele-tree',
          name: 'EleTree',
          component: () => import('../views/tree/EleTree'),
          meta: {
            title: ''
          }
        }
      ]
    },
    {
      path: '/excel',
      name: 'Excel',
      component: Layout,
      redirect: '/excel/export-excel',
      meta: {
        title: 'Excel',
        icon: 'vue-sys-icon-excel'
      },
      children: [
        {
          path: 'export-excel',
          name: 'ExportExcel',
          component: () => import('../views/excel/ExportExcel'),
          meta: {
            title: 'Excel'
          }
        },
        {
          path: 'import-excel',
          name: 'ImportExcel',
          component: () => import('../views/excel/ImportExcel'),
          meta: {
            title: 'Excel'
          }
        }
      ]
    }
  ]
})



